import React, { useState, useEffect } from 'react';
import { useQuery, useMutation } from 'react-query'

import { apiGETQuery, apiPUTMutate } from '../../api/queryLookup'

export const UpdateProfile = (props) => {
    const { profileUsername, requestUserId, userProfileRefetch} = props;
    const [initProfileData, setInitProfileData] = useState({
        'bio' : '',
        // 'date_of_birth' : '',
        // 'sex' : '',
    });
    const {data: profile, status} = useQuery(['userProfile', `accounts/${profileUsername}/update/`], apiGETQuery);
    const [update_mutate] = useMutation(apiPUTMutate, {
        onSuccess : userProfileRefetch
    });

    useEffect(() => {
        if(status==="success"){        
            setInitProfileData({
                'bio' : profile.bio,
                // 'date_of_birth' : profile.date_of_birth,
                // 'sex' : profile.sex,
            })
        }
    }, [profile, status])

    const handleChange = (event) => {
        event.preventDefault();
        setInitProfileData({
            ...initProfileData,
            [event.target.name] : event.target.value
        })
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        const endpoint = `accounts/${profileUsername}/update/`;
        const bio = initProfileData.bio
        // const date_of_birth = initProfileData.date_of_birth
        // const sex = initProfileData.sex
        const payload = {}
        if(bio){
            payload['bio'] = bio
        }
        // if(date_of_birth){
        //     payload['date_of_birth'] = date_of_birth
        // }
        // if(sex){
        //     payload['sex'] = sex
        // }
        try{
            update_mutate({endpoint, payload})
        }catch(error){
            console.log(error)
        }
    }

    if(status==="loading"){
        return (
            <div className="text-center">
                <h1>Loading...</h1>
            </div>
        )
    }

    if(status==="error"){
        return (
            <div className="text-center">
                <h1>Error. Try Again</h1>
            </div>
        )
    }

    if(status==="success"){
        if(profile.error_message==='not found'){
            return (
                <div className="text-center">
                    <h1>Error 404 : Not Found</h1>
                </div>
            )
        }
        return (
            <div className="card-custom form-custom profile-update">
                <form method="post" onSubmit={handleSubmit}>
                    <div className="form-group-custom">
                        <input 
                            maxLength={128}
                            name="bio"
                            type="text" 
                            placeholder=" "
                            value={initProfileData.bio}
                            onChange={handleChange}
                        />
                        <label className="label-custom">
                            <span className="label-text-custom">Bio</span>
                        </label>
                    </div>
                    {/* <div className="form-group-custom">
                        <input 
                            name="date_of_birth"
                            type="date" 
                            placeholder=" "
                            value={initProfileData.date_of_birth}
                            onChange={handleChange}
                        />
                        <label className="label-custom">
                            <span className="label-text-custom">Date of Birth</span>
                        </label>
                    </div> */}
                    <div className="mt-3">
                        <input type="submit" value="Save Changes" className="btn-custom" />
                    </div>
                </form>
            </div>
        )
    }
}